/* src/pages/TableView.css */
.table-view-wrapper {
    display: flex;
    min-height: 100vh;
    width: 100%;
}

.table-actions {
    width: 250px;
    background-color: #f8f9fa;
    padding: 20px;
    border-right: 1px solid #dee2e6;
}

.table-actions h3 {
    margin-top: 0;
    margin-bottom: 15px;
    font-size: 18px;
}

.action-btn {
    display: block;
    width: 100%;
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    background-color: white;
    cursor: pointer;
    text-align: left;
    transition: background-color 0.2s;
}

.action-btn:hover {
    background-color: #e9ecef;
}

.table-content {
    flex: 1;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 1350px; /* Увеличиваем ширину в 1.5 раза (900px * 1.5 = 1350px) */
}

.table-content h2 {
    margin-bottom: 15px;
}